/*PAGINA INZIIALE PECORSI*/

@import url("https://fonts.googleapis.com/css2?family=Baloo+2&display=swap");
$main-green: #79dd09 !default;
$main-green-rgb-015: rgba(121, 221, 9, 0.1) !default;
$main-yellow: #bdbb49 !default;
$main-yellow-rgb-015: rgba(189, 187, 73, 0.1) !default;
$main-red: #bd150b !default;
$main-red-rgb-015: rgba(189, 21, 11, 0.1) !default;
$main-orange: #f8823e !default;
$main-orange-rgb-015: rgba(189, 82, 11, 0.1) !default;
$main-blue: #0076bd !default;
$main-blue-rgb-015: rgba(0, 118, 189, 0.1) !default;

/* Cards */
.postcardpercorsi {
    flex-wrap: wrap;
    display: flex;

    box-shadow: 0 4px 21px -12px rgba(0, 0, 0, 0.66);
    border-radius: 10px;
    margin: 0 0 2rem 0;
    //overflow: hidden;
    position: relative;
    color: #000000;

    &.dark {
        background-color: #18151f;
    }
    &.light {
        background-color: #ffffff; //#e1e5ea;
    }
    &.timeline {
        background: linear-gradient(70deg, $main-orange-rgb-015, transparent 100%); //#e1e5ea;
    }

    .t-dark {
        color: #18151f;
    }

    a {
        color: inherit;
    }

    h1,
    .h1 {
        margin-bottom: 0.5rem;
        font-weight: 500;
        line-height: 1.2;
    }

    .small {
        font-size: 80%;
    }

    .postcardpercorsi__title {
        text-align: center;
        font-size: 1.75rem;
    }

    .postcardpercorsi__img {
        //max-height: 220px;
        //width: 100%;
        //object-fit: cover;
        position: relative;
    }

    .postcardpercorsi__img_link {
        display: contents;
    }

    .postcardpercorsi__bar {
        width: 50px;
        height: 10px;
        margin: 10px 0;
        border-radius: 5px;
        background-color: #424242;
        transition: width 0.2s ease;
    }

    .postcardtimeline__bar {
        width: 100px;
        height: 5px;
        margin: 10px 0;
        border-radius: 5px;
        background-color: #424242;
        transition: width 0.2s ease;
    }

    .postcardpercorsi__text {
        padding: 1.5rem;
        position: relative;
        display: flex;
        flex-direction: column;
    }

    .postcardpercorsi__preview-txt {
        overflow: hidden;
        text-overflow: ellipsis;
        text-align: justify;
        //height: 100%;
    }

    .postcardpercorsi__preview-txt__schedaItinerarioPredefinito {
        overflow: hidden;
        text-overflow: ellipsis;
        text-align: left;
        //height: 100%;
    }

    .postcardpercorsi__tagbox {
        display: flex;
        flex-flow: row wrap;
        font-size: 14px;
        margin: 20px 0 0 0;
        padding: 0;
        justify-content: center;

        .tag__item {
            display: inline-block;
            background: rgba(194, 194, 194, 0.4);
            border-radius: 3px;
            padding: 2.5px 10px;
            margin: 0 5px 5px 0;
            cursor: pointer;
            user-select: none;
            transition: background-color 0.3s;

            &:hover {
                background: #9eca58cc;
            }
        }
    }

    .userchoice__tagbox {
        display: flex;
        flex-flow: row wrap;
        font-size: 14px;
        margin: 10px 0 0 0;
            padding: 0;
        justify-content: center;
    
        .tag__item__selected {
          display: inline-block;
          background: #f09b56cc;
          border-radius: 3px;
          padding: 2.5px 10px;
          margin: 0 5px 5px 0;
          cursor: pointer;
          user-select: none;
          transition: background-color 0.3s;
      
          &:hover {
            background: #f09b56cc;
          }
        }
      
        .tag__item__unselected {
          display: inline-block;
          background: rgba(194, 194, 194, 0.4);
          border-radius: 3px;
          padding: 2.5px 10px;
          margin: 0 5px 5px 0;
          cursor: pointer;
          user-select: none;
          transition: background-color 0.3s;
      
          &:hover {
            background: #f09b56cc;
          }
        }
      }

    &:before {
        content: "";
        position: absolute;
        top: 0;
        right: 0;
        bottom: 0;
        left: 0;
        background-image: linear-gradient(-70deg, #424242, transparent 50%);
        opacity: 1;
        border-radius: 10px;
    }

    &:hover .postcardpercorsi__bar {
        width: 100px;
    }

    &:hover .postcardtimeline__bar {
        width: 200px;
    }

    &:hover .postcardattivita__bar {
        width: 200px;
    }
}

@media screen and (min-width: 769px) {
    .postcardpercorsi {
        flex-wrap: inherit;

        .postcardpercorsi__title {
            text-align: left;
            font-size: 2rem;
        }

        .postcardpercorsi__tagbox {
            justify-content: start;
        }

        .userchoice__tagbox {
            justify-content: start;
        }

        .postcardpercorsi__img {
            max-width: 300px;
            max-height: 100%;
            transition: transform 0.3s ease;
        }

        .postcardtimeline__img {
            //max-width: 100%;
            max-height: 100%;
            transition: transform 0.3s ease;
        }

        .postcardpercorsi__text {
            padding-top: 3rem;
            padding-left: 3rem;
            padding-right: 3rem;
            width: 100%;
        }

        .media.postcardpercorsi__text:before {
            content: "";
            position: absolute;
            display: block;
            background: #18151f;
            top: -20%;
            height: 130%;
            width: 55px;
        }

        &:hover .postcardpercorsi__img {
            transform: scale(1.1);
        }

        &:hover .postcardtimeline__img {
            transform: scale(1.1);
        }

        &:nth-child(2n + 1) {
            flex-direction: row;
        }

        &:nth-child(2n + 0) {
            flex-direction: row-reverse;
        }

        &:nth-child(2n + 1) .postcardpercorsi__text::before {
            left: -12px !important;
            transform: rotate(4deg);
        }

        &:nth-child(2n + 0) .postcardpercorsi__text::before {
            right: -12px !important;
            transform: rotate(-4deg);
        }
    }
}

@media screen and (min-width: 1024px) {
    .postcardpercorsi__text {
        padding: 2rem 3.5rem;
    }

    // .postcardpercorsi__text:before {
    //   content: "";
    //   position: absolute;
    //   display: block;

    //   margin-left: -1px;
    //   top: -20%;
    //   height: 130%;
    //   width: 35px;
    // }

    .postcardpercorsi.dark {
        .postcardpercorsi__text:before {
            background: #18151f;
        }
    }
    .postcardpercorsi.light {
        .postcardpercorsi__text:before {
            background: #ffffff; //#e1e5ea;
        }
    }
}

